<template>
	<div id="tree3"></div>
</template>

<style>
#tree3 {
	width: 95%;
    height: 100%;
    /* background-color: aliceblue; */
    margin:1%
	
}
</style>

<script type="text/javascript">
import * as echarts from 'echarts'
import axios from 'axios';
export default {
	data() {
		return {
			relation: []
		}
	},
	mounted() {
		var __this = this;
		/*var data = d3.json("../echarts_dot.json", function (csvdata) {
			return csvdata;
		}).then(function (data) {*/
		axios.get('/analyze_all1.json').then((data1) => {
			console.log(data1)
			let data = data1.data;
			var dom = document.getElementById('tree3');
			var myChart = echarts.init(dom, 'null', {
				renderer: 'canvas',
				useDirtyRect: false
			});
			//var app = {};

			//var option;

			console.log(data);
			var option = {
    title: {
        text: 'callStackRecords-未验证',
    },
    tooltip: {
        trigger: 'item',
        triggerOn: 'mousemove'
    },
    series: [{
        height: "150%",
        width:"90%",
        type: 'tree',
        roam: true,
        layout: 'orthogonal',
        orient: 'LR',
        symbol: 'emptyCircle',
        symbolSize: [10,10],
        left:80,
        right:120,
        initialTreeDepth: 1,
        // nodePadding: 10,
        label: {
            show: true,
            offset: [10, 0],
            align: 'left',
            formatter: function(params) {
                var text = params.name;
                var length = text.length;
                var lineHeight = 16; // 每行的高度
                var maxLineLength = 100; // 每行最多显示的字符数
                var lineCount = Math.ceil(length / maxLineLength); // 计算需要几行
                var lines=[];
                for (var i = 0; i < lineCount; i++) {
                    var line = text.substr(i * maxLineLength, maxLineLength);
                    lines.push(line);
                }
                return lines.join('\n')
                // var lines = text.split(': ');
                // return lines.join(':\n');
            }

        },
        tooltip: {
            formatter: function(params){ return params.value.replace('/r/n','<br />')}
        },
        
        data: [{
            name: 'callStackRecords',
            value: 'hasCallChain: true\n\ncallChainCount: 3',
            symbol: 'roundRect',
            symbolSize: 15,
            label: {
                offset: [0, -15]
            },
            children: data
        }],
    }]
};

			myChart.setOption(option);


		})

	},
}
</script>